import "./Register.css";
import { useEffect } from "react";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
const axios = require('axios');

function Register(props) {
    const navigate = useNavigate();
    useEffect(async () => {
  
    }, [])
  
  
    const urlparams = new URLSearchParams(window.location.search);
    const roomId = urlparams.get("id");
    const isRedirected = sessionStorage.getItem('redirected');

        function register(user) {
            // alert('test')
            axios.post('http://ec2-3-23-177-89.us-east-2.compute.amazonaws.com:8080/register', {
                "name": document.getElementById('name').value,
                "email": document.getElementById('email').value,
                "section": document.getElementById('section').value,
                "password": document.getElementById('password').value
            })
            .then(function (response) {
                    navigate({ pathname: "/login", search: "?id=" + roomId})
                    console.log(response);
            })
            .catch(function (error) {
                console.log(error);
            });
        }
        return (
            <body>
                <div className="container">
                    <div class="form" />
                    <span class="title">Registration Form</span>

                    <form action="#">
                        <div class="input-field">
                            <input id="name" type="text" placeholder="Enter your name" required />
                            <i class="uil uil-user"></i>
                        </div>
                        <div class="input-field">
                            <input id="email" type="text" placeholder="Enter your email" required />
                            <i class="uil uil-envelope icon"></i>
                        </div>
                        <div class="input-field">
                            <input id="section" type="text" placeholder="Enter your section" required />
                            <i class="uil uil-envelope icon"></i>
                        </div>
                        <div class="input-field">
                            <input id="password" type="password" class="password" placeholder="Create a password" required />
                            <i class="uil uil-lock icon"></i>
                        </div>
                        <div class="input-field">
                            <input type="password" class="password" placeholder="Confirm a password" required />
                            <i class="uil uil-lock icon"></i>
                            <i class="uil uil-eye-slash showHidePw"></i>
                        </div>

                        <div class="checkbox-text">
                            <div class="checkbox-content">
                                <input type="checkbox" id="sigCheck" />
                                <label for="sigCheck" class="text">Remember me</label>
                            </div>

                            <a href="#" class="text">Forgot password?</a>
                        </div>


                        <div class="input-field button">
                            <input type="button" value="Login Now" onClick={() => {
                                navigate({ pathname: "/login", search: "?id=" + roomId})
                            }} />
                            <input type="button" onClick={register} value="Register" />
                        </div>
                        <div class="input-field button">
                        </div>
                    </form>
                </div>

                
{/* 
                <div className="Register">
                    <button onClick={() => {
                        sessionStorage.setItem('userName', document.getElementById('userName').value);
                        navigate({ pathname: "/app", search: "?id=" + roomId });
                    }}>{isRedirected ? 'Login Now' : 'Sign Up'}</button>
                    <input id="userName" />
                </div> */}
            </body>
        )
}
export default Register;