import MainScreen from "./components/MainScreen/MainScreen.component";
import firepadRef, { db } from "./server/firebase";
import "./App.css";
import { useEffect } from "react";
import {
  setMainStream,
  addParticipant,
  setUser,
  removeParticipant,
  updateParticipant,
} from "./store/actioncreator";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
const axios = require('axios');

function App(props) {
  const navigate = useNavigate();

  const userName = sessionStorage.getItem("userName");
  const urlparams = new URLSearchParams(window.location.search);
  const roomId = urlparams.get("id");



  const getUserStream = async () => {
    const localStream = await navigator.mediaDevices.getUserMedia({
      audio: true,
      video: true,
    });

    return localStream;
  };
  useEffect(async () => {


    if( !sessionStorage.getItem('userName') ) {
      sessionStorage.setItem('redirected', true) 
      navigate({pathname: "/", search: "?id=" + roomId});
    } else {

      axios.post('http://ec2-3-23-177-89.us-east-2.compute.amazonaws.com:8080/start', 
        {
            "meetingId": roomId,
            "userName": userName
        
        })
        .then(function (response) {
                console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
      const stream = await getUserStream();
      stream.getVideoTracks()[0].enabled = false;
      props.setMainStream(stream);
  
      connectedRef.on("value", (snap) => {
        if (snap.val()) {
          const defaultPreference = {
            audio: true,
            video: false,
            screen: false,
          };
          const userStatusRef = participantRef.push({
            userName,
            preferences: defaultPreference,
          });
          props.setUser({
            [userStatusRef.key]: { name: userName, ...defaultPreference },
          });
          userStatusRef.onDisconnect().remove();
        }
      });
    }
  }, []);

  const connectedRef = db.database().ref(".info/connected");
  const participantRef = firepadRef.child("participants");

  const isUserSet = !!props.user;
  const isStreamSet = !!props.stream;

  useEffect(() => {
    if (isStreamSet && isUserSet) {
      participantRef.on("child_added", (snap) => {
        const preferenceUpdateEvent = participantRef
          .child(snap.key)
          .child("preferences");
        preferenceUpdateEvent.on("child_changed", (preferenceSnap) => {
          props.updateParticipant({
            [snap.key]: {
              [preferenceSnap.key]: preferenceSnap.val(),
            },
          });
        });
        const { userName: name, preferences = {} } = snap.val();
        props.addParticipant({
          [snap.key]: {
            name,
            ...preferences,
          },
        });
      });
      participantRef.on("child_removed", (snap) => {
        const { userName: name, preferences = {} } = snap.val();
        axios.post('http://ec2-3-23-177-89.us-east-2.compute.amazonaws.com:8080/end', 
        {
            "meetingId": roomId,
            "userName": name
        
        })
        .then(function (response) {
                console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
        props.removeParticipant(snap.key);
      });
    }
  }, [isStreamSet, isUserSet]);

  return (
    <div className="App">
      <MainScreen />
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    stream: state.mainStream,
    user: state.currentUser,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setMainStream: (stream) => dispatch(setMainStream(stream)),
    addParticipant: (user) => dispatch(addParticipant(user)),
    setUser: (user) => dispatch(setUser(user)),
    removeParticipant: (userId) => dispatch(removeParticipant(userId)),
    updateParticipant: (user) => dispatch(updateParticipant(user)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
