import "./LoginPage.css"
import { useEffect } from "react";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
const axios = require('axios');

function LoginPage(props) {

    const navigate = useNavigate();

    // useEffect(async () => {
  
    // }, [])
  

    const urlparams = new URLSearchParams(window.location.search);
    const roomId = urlparams.get("id");
    const isRedirected = sessionStorage.getItem('redirected');
    function login()
    {
        axios.post('http://ec2-3-23-177-89.us-east-2.compute.amazonaws.com:8080/login', {
            "email": document.getElementById('email').value,
            "password": document.getElementById('password').value
        })
          .then(function (response) {
                
                sessionStorage.setItem('userName', response.data.name);
                navigate({ pathname: "/main", search: "?id=" + roomId})
                console.log(response);
          })
          .catch(function (error) {
              alert("Invalid User")
            console.log(error);
          }); 
    }
  
    return  (<div className="container">
        <div class="forms"/>
            <div class="form login"/>
                <span class="title">Login</span>

                <form action="#">
                    <div class="input-field">
                        <input id="email" type="text" placeholder="Enter your email" required/>
                        <i class="uil uil-envelope icon"></i>
                    </div>
                    <div class="input-field">
                        <input id="password" type="password" class="password" placeholder="Enter your password" required/>
                        <i class="uil uil-lock icon"></i>
                        <i class="uil uil-eye-slash showHidePw"></i>
                    </div>

                    <div class="checkbox-text">
                        <div class="checkbox-content">
                            <input type="checkbox" id="logCheck"/>
                            <label for="logCheck" class="text">Remember me</label>
                        </div>
                        
                        <a href="#" class="text">Forgot password?</a>
                    </div>

                    <div class="input-field button">
                        <input type="button" value="Login Now" onClick={login}/>
                    </div>
                </form>

                <div class="login-signup">
                    <span class="text">Not a member?
                        <a href="#" class="text signup-link" onClick={() => {
                            navigate({ pathname: "/register", search: "?id=" + roomId})
                        }}>Signup now</a>
                    </span>
                </div>
            </div>
    );
}


export default LoginPage;